.logo img {
  width: 100px;
  height: 100px;
}

.app-wrapper {
  display: grid;
  grid-template-areas:
          "h h"
          "n c"
          "f f";

  grid-template-rows: 100px 1fr ;
  grid-template-columns: 2fr 10fr;

}


.app-wrapper-content {
    grid-area: c;
    background-color: orange;
}